import { FormEventHandler, MouseEventHandler, useState } from 'react';
import { Helmet } from 'react-helmet-async';
import { t } from '../../../helpers/i18n/dictionary';
import useAuth from '../../../hooks/auth/useAuth';
import ActionButton from '../../Buttons/ActionButton';
import { MinimalLayout } from '../../Layout/Layout';
import UrlNotifier from '../../Layout/UrlNotifier/UrlNotifier';

const Login = () => {
  const [password, setPassword] = useState('a');
  const [state, setState] = useState<'loading' | 'error' | 'success' | undefined>();
  const { authenticate, forceSetPassword_temp } = useAuth();

  const forcePassword: MouseEventHandler = async (e) => {
    e.preventDefault();

    const success = await forceSetPassword_temp('a');
    if (success) {
      setPassword('a');
      alert('Password was successfully set to a');
    }

    return false;
  };

  const doLogin: FormEventHandler = async (e) => {
    e.preventDefault();
    setState('loading');
    try {
      await authenticate(password);
      setState('success');
    } catch (ex) {
      // Todo show message what failed
      console.error(ex);
      setState('error');
    }
    return false;
  };

  return (
    <>
      <Helmet>
        <title>Login | Owner console</title>
      </Helmet>
      <MinimalLayout>
        <UrlNotifier />
        <section className="body-font flex h-full pt-24 ">
          <div className="container m-auto h-full max-w-[35rem] p-5">
            <div className="">
              <form onSubmit={doLogin}>
                <h1 className="mb-5 text-4xl dark:text-white">
                  YouAuth | Login to {window.location.hostname}
                </h1>

                <div className="mb-4">
                  <label htmlFor="youniverse-id" className="text-sm leading-7  dark:text-gray-400">
                    Youniverse id
                  </label>

                  <input
                    type="text"
                    name="youniverse-id"
                    id="youniverse-id"
                    required
                    className="w-full rounded border border-gray-300 bg-gray-100 bg-opacity-50 py-1 px-3 text-base leading-8 text-gray-700 outline-none transition-colors duration-200 ease-in-out focus:border-indigo-500 focus:bg-transparent focus:ring-2 focus:ring-indigo-200 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-100"
                    defaultValue={window.location.hostname}
                    disabled={true}
                    readOnly={true}
                  />
                </div>
                <div className="mb-4">
                  <label htmlFor="password" className="text-sm leading-7  dark:text-gray-400">
                    Password
                  </label>
                  <input
                    type="text"
                    name="password"
                    id="password"
                    required
                    className="w-full rounded border border-gray-300 bg-gray-100 bg-opacity-50 py-1 px-3 text-base leading-8 text-gray-700 outline-none transition-colors duration-200 ease-in-out focus:border-indigo-500 focus:bg-transparent focus:ring-2 focus:ring-indigo-200 dark:border-gray-700 dark:bg-gray-800 dark:text-gray-100"
                    onChange={(e) => setPassword(e.target.value)}
                    defaultValue={password}
                  />
                </div>
                <ActionButton
                  className="mt-10 w-full"
                  type="primary"
                  icon="send"
                  state={state}
                  size="large"
                >
                  {t('login')}
                </ActionButton>

                <ActionButton
                  className="mt-2 block w-full"
                  type="secondary"
                  onClick={forcePassword}
                >
                  {t("Force password to 'a'")}
                </ActionButton>
              </form>
            </div>
          </div>
        </section>
      </MinimalLayout>
    </>
  );
};

export default Login;
